import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import Img from 'gatsby-image';
import { withStyles } from 'material-ui/styles';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';

import TagArray from '../tag-array';

const propTypes = {

};

const styles = theme => ({
  root: {
    padding: 16,
    textAlign: 'left',
    color: theme.palette.text.secondary,
    marginTop: 20,
    transition: `all ${(theme.transitions.duration.standard * 0.001).toFixed(2)}s ${theme.transitions.easing.easeInOut}`,
    borderRadius: 2,
    '&:hover': {
      marginTop: 16,
      marginBottom: 4,
      boxShadow: theme.shadows[6],
      cursor: 'pointer',
    },
  },
  a: {
    textDecoration: 'none',
    '& h2': {
      color: theme.palette.primary[900],
      borderBottom: `3px solid ${theme.palette.primary[600]}`,
      paddingBottom: '2px',
      '&:hover': {
        color: theme.palette.primary[300],
        borderBottom: `5px solid ${theme.palette.primary[300]}`,
        paddingBottom: '0',
      },
    },
  },
});

const PostExcerpt = (props) => {
  const { classes, cover, excerpt, html, path, title, tags } = props;
  return (
    <Link className={classes.a} to={path}>
      <Paper className={classes.root}>
        <Typography type="title" gutterBottom>{title}</Typography>
        {/*cover ? (
          <Img responsiveSizes={cover} alt={title} />
        ) : null*/}
        <Typography type="body1">{excerpt}</Typography>
        <TagArray tags={tags} />
      </Paper>
    </Link>
  );
};

PostExcerpt.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostExcerpt);



