import React from 'react';
import Grid from 'material-ui/Grid';

import GridItem from '../components/grid-item';
import PostExcerpt from '../components/post-excerpt';
import TagBar from '../components/tag-bar';

export default class TagIndex extends React.Component {

  getPostList() {
    const postList = [];

    const qTag = this.props.location.search.slice(3);
    this.props.data.allMarkdownRemark.edges.forEach((edge) => {

      if (edge.node.frontmatter.tags.indexOf(qTag) !== -1) {
        postList.push({
          excerpt: edge.node.excerpt,
          slug: edge.node.fields.slug,
          path: edge.node.frontmatter.path,
          title: edge.node.frontmatter.title,
          date: edge.node.frontmatter.date,
          tags: edge.node.frontmatter.tags,
          cover: edge.node.frontmatter.cover
            ? edge.node.frontmatter.cover.childImageSharp.responsiveSizes
            : null,
        });
      }
    });
    return postList;
  }

  getTagList() {
    const tagList = [];
    this.props.data.allMarkdownRemark.edges.forEach((edge) => {
      const tmpArr = edge.node.frontmatter.tags.split(',');
      tagList.push(...tmpArr);
    });
    return Array.from(new Set(tagList));
  }

  render() {
    const qTag = this.props.location.search.slice(1);
    const tagList = this.getTagList();
    const postList = this.getPostList();

    return (
      <div>
        <TagBar tagList={tagList} qTag={qTag} />
        <Grid container spacing={0}>
          {postList.map(page => (
            <GridItem key={page.path} page={page} PostType={PostExcerpt} />
          ))}
        </Grid>
      </div>
    );
  }
}

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query TagIndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      limit: 2000,
      sort: { fields: [frontmatter___date], order: DESC },
    ) {
      edges {
        node {
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          frontmatter {
            title
            date
            path
            parent
            tags
            cover {
              childImageSharp {
                responsiveSizes(maxWidth: 670){
                base64
                aspectRatio
                src
                srcSet
                sizes
                originalImg
                originalName
                }
              }
            }
          }
        }
      }
    }
  }
`;



